.testimonial-slider-container-wrapper {
    overflow: hidden;
    min-height: 100vh;
    width: 100%;
    background-color: #010101;
    background-size: cover;
    background-position: center center;
  }
  
  .testimonial-slider-box{
    margin-left: -15px;
    margin-right: -15px;
  }
  
  .testimonial-slider {
    position: relative;
    float: left;
    width: 105vw;
    min-height: 100vh;
    padding-bottom: 15px;
    transition: 1s;
    /* transition-timing-function: cubic-bezier(.79,.12,.4,1.03); */
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    margin-left: 0;
  }
  
  .testimonial-slider-content-box {
    height: 100%;
  }
  
  .testimonial-quote-text-wrapper{
      margin-left: 0px !important;
  }

  .testimonial-quote > div > img {
    width: 75px;
    height: 75px;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .testimonial-author-img {
    height: 140px;
    width: 140px;
    border-radius: 50%;
    margin-bottom: 1em;
    border: 2px solid #878787;
    padding: 5px;
    overflow: hidden;
  }
  
  .testimonial-author-img > div {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
  }
  
  .testimonial-quote-text {
    color: #e5e5e5;
    font-family: 'Poiret One', cursive;
    letter-spacing: 3px;
    line-height: 100%;
    font-size: 20px;
    text-align: center;
  }
  
  .testimonial-author-name {
    font-size: 25px;
    color: #9b9381;
  }
  
  .testimonial-author-name {
    font-size: 25px;
  }
  .testimonial-author-designation{
      color: #7d7769;
  }