.footer-wrapper{
  /* background: url('../../../../assets/images/footer-bg.jpg'); */
  background-position: 0% 0%;
  background-size: 100% 100%;
  background-repeat: no-repeat;
}

.footer-wrapper-effect{
  position: relative;
  top:0px;
  left: 0px;
  width: 100%;
}
.footer-wrapper-effect > .row{
  position: relative;
}
.footer-wrapper-effect::before{
  content: " ";
  position: absolute;
  top:0px;
  left: 0px;
  width: 100%;
  bottom: 0px;
  background-color: #010101bb;
}
.footer-container{
  height: 100%;
}

.footer-header-time{
  margin-top: 1.75em;
  margin-bottom: 0em;
  font-weight: 100;
  text-transform: uppercase;
  font-size: 2.5em;
  line-height: 100%;
}

.footer-service-fapor{
  margin-top: -1.75em;
}

.footer-project-availability{
  font-size: 1.25em;
  font-weight: 200;
  margin: 0px;
  line-height: 100%;
}
.footer-project-fapor{
  font-size: 1.75em;
  font-weight: 800;
  text-transform: uppercase;
  line-height: 100%;
  margin-top: 0em;
}

.footer-project-fapor strong{
  margin-left: -10px;
  font-size: 4em;
  font-weight: 600;
}

.footer-project-fapor-v2{
  text-transform: uppercase;
  font-weight: 400;
  font-size: 2em;
  letter-spacing: 1px;
  margin-top:0px;
}

.footer-line-animation-box{
  width: 240px;
  height: 80px;
  border: 2px solid #f75e50;
  border-top: 0px;
  margin-top: -48px;
}
.footer-line-animation-box::before{
  display: inline-block;
  margin: 0 0 15px 0px;
  height: 2px;
  content: " ";
  text-shadow: none;
  background-color: #f75e50;
  width: 65px;
}

.footer-line-animation-box::after{
  display: inline-block;
  margin: 0 0 15px 112px;
  height: 2px;
  content: " ";
  text-shadow: none;
  background-color: #f75e50;
  width: 59px;
}

.footer-contact-button{
  margin-top: -21px;
}

.footer-contact-button .red-button-class{
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 1px;
}

.footer-or{
  font-weight: 100;
  font-size: 42px;
  margin-bottom: 0px;
  line-height: 100%;
  margin-top: 10px;
}

.footer-fapor-discover{
  font-weight: 400;
  font-size: 28px;
  margin-bottom: 0px;
  margin-top: 15px;
  line-height: 100%;
  text-transform: uppercase;
  letter-spacing: 10px;
}

.footer-social-icon-wrapper{
  margin-top:20px;
}

.footer-social-icon{
  color: #222;
  font-size: 18px;
  transition: 0.3s;
  cursor: pointer;
}

.footer-social-icon-holder{
  height: 34px;
  width: 34px;
  background-color: #f5f5f5;
  padding: 5px;
  padding-top: 6.5px;
  text-align: center;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.5s;
  margin-left: 7.5px;
  margin-right: 7.5px;
}

.footer-social-icon-holder:hover{
  background-color: #f75e50;
}

.footer-social-icon-holder:hover .footer-social-icon{
  color: #f5f5f5;
}

.footer-social-menu-wrapper{
  margin-top: 20px;
}

.footer-menu{
    width: 100%;
}

.footer-menu-item{
  font-size: 14px;
  color: #878787;
  transition: 0.3s;
  letter-spacing: 1px;
  font-weight: 600;
  cursor: pointer;
}

.footer-menu-item:hover{
  color: #f75e50;
}

.footer-carocel-img-wrapper{
  margin-top: 20px;
}

.footer-carocel-img img{
  width: 100%;
  height: 300px;
}

.footer-img-wrapper{
  height: width/3;
}
.footer-img-container{
  height: 100%;
}

.footer-img{
  width: 100%;
  height: 100%;
}
.footer-bottom-wrapper{
  height: 100%;
}
.footer-bottom-copyright{
  height: 100%;
  display: flex;
}

.footer-bottom-container{
  height: 30px;
  background-color: #222;
}

.footer-bottom-container-row{
  height: 100%;
}

.footer-copyright-text{
  font-size: 10px;
  font-weight: 400;
  margin: 0px;
  color:#878787;
  text-transform: capitalize;
  letter-spacing: 1px;
  padding-top:10px;
  padding-bottom: 5px;
}

.footer-bottom-link{
  font-size: 10px;
  font-weight: 400;
  margin: 0px;
  color:#878787;
  text-transform: capitalize;
  letter-spacing: 1px;
  cursor: pointer;
  background: none;
}
.footer-bottom-link:hover{
  background: none !important;
}
.footer-carosel-container{
  margin-top:20px;
}

.footer-bottom{
  align-self: flex-end;
}
.footer-top{
  align-self: flex-end;
}


.footer-modal{
  background-color: #222;
  max-height: 80vh;
  color: #f5f5f5;
}

/* CAROUSEL*/

.carousel-item img{
    width: 100%;
    object-fit: contain;
}

.carousel > .active{
    background: none;
}

#materialbox-overlay{
    /* width: 98vw !important; */
    background-color: #010101bb;
}

/*smartphone*/
@media screen and (min-width: 576px) and (max-width: 767){
  .footer-menu-item{
      font-size: 11px;
  }
}

/*smallphone*/
@media (max-width: 575px){
  .footer-menu-item{
      font-size: 10px;
  }
  .footer-header-time{
      font-size:1em;
  }

  .footer-project-fapor{
      font-size:1em;
      margin-top: 0.75em;
  }

  .footer-fapor-discover{
      font-size: 0.80em;
  }
}